import React, { useState, useEffect } from "react";
import styled from "styled-components";
import axios from "axios";

import SearchCourseItem from "./SearchCourseItem";
import CourseLists from "./CourseLists";

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  overflow-y: auto;
  overflow-x: visible;
  &::-webkit-scrollbar {
    width: 10px;
    height: 5px;
    boder-radius: 10px;
  }
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0, 0);
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.2);
    background-clip: padding-box;
    border: 2px solid transparent;
    border-radius: 100px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.3);
    background-clip: padding-box;
    border: 1.5px solid transparent;
    border-radius: 100px;
  }
`;

const Table = styled.table`
  width: 100%;
  background-color: white;
  margin: 0px;
  padding: 0px;
  border-spacing: 0px;
  border-style: none;
`;

const Tr = styled.tr`
  width: 100%;
`;

const Th = styled.th`
  padding: 0.45vw;
  padding-bottom: 0.23vw;
  margin: 0px;
  font-size: 0.9rem;
  background-color: #ffcc1d;
  color: #ffffff;
  border: 0px;
  position: sticky;
  top: 0;
`;

function SearchCourseList(props) {
  // useEffect(() => {
  //   axios.get("/api/test").then((res) => {
  //     setCourseLists(res.data.content);
  //   });
  // }, []);
  return (
    <Wrapper>
      <Table>
        <Tr>
          <Th name="id" style={{ borderTopLeftRadius: "15px" }}>
            No.
          </Th>
          <Th name="department"></Th>
          <Th name="grade"></Th>
          <Th name="divison"></Th>
          <Th name="subject_id"></Th>
          <Th name="subject_type"></Th>
          <Th name="grades"></Th>
          <Th name="max_count"></Th>
          <Th name="register_count"></Th>
          <Th name="subject_time"></Th>
          <Th name="professor" style={{ borderTopRightRadius: "15px" }}>
            
          </Th>
          {/*<Th name="courseSortation"></Th>
                    <Th name="coursePreRequest">()</Th>--------------------
                    <Th name="courseTheory"></Th>
                    <Th name="coursePractice"></Th>
                    <Th name="courseArea"></Th>
                    <Th name="courseNote" style={{borderTopRightRadius:'15px'}}></Th> */}
          {/* <Th name="courseDistribution"></Th> */}
        </Tr>
        {/* {courseLists.map((entrie) => (
          <SearchCourseItem key={entrie.subject_id} courseList={entrie} />
        ))} */}
        {/* {Object.entries(courseLists).map((entrie) => (
          <SearchCourseItem key={entrie.subject_id} courseList={entrie} />
        ))} */}
        <div>{props.courseList}</div>
      </Table>
    </Wrapper>
  );
}

export default SearchCourseList;
